<script>
import { Layout, FluroAvatarUpdate } from 'fluro-vue-ui';




export default {
    components: {
        FluroAvatarUpdate,
    },
    props: {
        id: {
            type: String,
            required: true,
        }
    },
    data() {
        return {
            loading: true
        }
    },
    // created() {
    // console.log('TESTING', this.$route)
    // },
    // mounted() {
    // console.log('MOUNTED')
    // },
    // activated() {
    //     console.log('ACTIVATED')
    // },
    // deactivated() {
    //     console.log('DeACTIVATED')
    // },
    computed: {
        readableDefinition() {
            return this.$fluro.types.readable(this.item.definition || 'contact');
        },
        birthday() {
            return this.$fluro.date.formatDate(this.item.dob, 'D MMM YYYY')
        },
        homePhoneNumbers() {
            return this.item.family.phoneNumbers || [];
        },
        emails() {
            return this.item.emails || [];
        },

        phoneNumbers() {
            return this.item.local || this.item.international || this.item.phoneNumbers || [];
        },
        householdPhoneNumbers() {
            return this.item.householdPhoneNumbers || [];
        },
        capabilities() {
            var self = this;
            return self.mapDefinitionItems(self.item.capabilities, 'capability')
        },
        tags() {
            var self = this;
            return self.mapDefinitionItems(self.item.tags, 'tag')
        },
        realms() {
            var self = this;

            var filtered = _.chain(self.item.realms)
                .filter(function(realm) {
                    return !realm._discriminatorType;
                })
                .orderBy(function(realm) {
                    return realm.title;
                })
                .value();

            return self.mapDefinitionItems(filtered, 'realm')
        },
        locations() {
            var self = this;
            return self.mapDefinitionItems(self.item.locations, 'location')
        },
    },
    methods: {
        definitionName(key, plural) {

        },
        mapDefinitionItems(array, backup) {

            var self = this;

            if (!array || !array.length) {
                return;
            }

            return _.chain(array)
            .reduce(function(set, entry) {

                var key = entry.definition || backup;
                var existing = set[key];
                if (!existing) {
                    existing = set[key] = {
                        // title: self.$fluro.types.readable(key, true),
                        key,
                        entries: [],
                    }
                }
                existing.entries.push(entry);
                return set;
            }, {})
            .values()
            .orderBy(function(type) {
                return type.key == backup
            })
            .value();
        }
    },
    asyncComputed: {
        item: {
            get() {

                // console.log('LOAD DETAILS', this)
                var self = this;
                self.loading = true;

                return new Promise(function(resolve, reject) {


                    return self.$fluro.content.get(self.id)
                        .then(function(item) {


                            resolve(item);
                            self.loading = false;
                        })
                        .catch(function(err) {
                            reject(err);
                            self.loading = false;
                        })

                })
            }
        }
    }
}
</script>
